//
import Vue from 'vue'
import VueRouter from 'vue-router'
import PC from "../pages/PC"
import Mobile from "../pages/Mobile"
//vue-router
Vue.use(VueRouter)

const router = new VueRouter({
    mode: 'history',
    routes:[
        //
        {
            path:'/pc',
            component:PC,
        },
        {
            path: '/mobile',
            component: Mobile,
        }
    ]
})
//
//
//
router.beforeEach((to, from, next) => {


    if (to.path==="/"){
        let code = to.query.code;
        if (code){
            localStorage.setItem('code',code)
            console.log("code")
        }else {
            console.error(`code,url=${window.location.href}`)
        }
    }

    let code = localStorage.getItem("code");
    if (!code){
        console.error("vueRouter code")
        next(false)
    }else {
        // const isMobile =  /Android|webOS|iPhone|iPad|iPod|BlackBerry/i.test(navigator.userAgent)
        const isMobile = true;
        if (isMobile) {
            // /mobile
            if (to.path !== '/mobile') {
                next('/mobile')
            } else {
                next()
            }
        } else {
            // PC/pc
            if (to.path !== '/pc') {
                next('/pc')
            } else {
                next()
            }
        }
    }
})

//
export default router
