<template>
    <div id="app">
        <router-view></router-view>
    </div>
</template>

<script>
    import Vue from "vue";
    export default {
        name: 'App',
        created() {
            let code = localStorage.getItem("code");
            if (code) {
                this.$toast(`Appcode=${code}`)
            } else {
                this.$toast.fail({
                    message: `Appcode,url=${window.location.href}`,
                    closeOnClickOverlay: true,
                    duration: 5000
                });

                this.$destroy()
            }
        }
    }
</script>

<style>
    html, body {
        height: 100%;
        width: 100%;
    }

    #app {
        height: 100%;
        width: 100%;
    }
</style>
