<template>
    <div>
        <m-chat
                ref="mChat"
                :messages="messages"
                @submit="submit"
                :loadMore="loadMore"
                height="100vh"
                :config="config"
                :comment="true"
                @extendItemClick="extendItemClick"
                :imgMaxSize="204800" :videoMaxSize="204800" :fileMaxSize="204800"
        >
        </m-chat>
    </div>


</template>

<script>
    import axios from "axios";
    import api from '../../api'
    import time from "../../utils/time";
    import fileUtil from "../../utils/fileUtil";
    import ajax from "../../api/ajax";

    export default {
        name: "mobile-index",
        data() {
            return {
                config: {
                    /**
                     *  'light'()|'dark'()
                     */
                    theme: 'light',
                    /**
                     * 
                     */
                    image: {
                        /**
                         * 
                         */
                        accept: ['image/*']
                    },
                    /**
                     * 
                     */
                    file: {
                        accept: ['*']
                    },
                    /**
                     * 
                     */
                    video: {
                        accept: ['video/*']
                    },
                    /**
                     *  true
                     */
                    isImagePreview: true,
                    /**
                     * vant ImagePreview 
                     */
                    imagePreviewConfig: {
                        closeable: false
                    },
                    openExtends: ['refresh','clear'],
                    /**
                     * item
                     * vant-icon https://vant-contrib.gitee.io/vant/v2/#/zh-CN/icon#zi-ding-yi-tu-biao
                     */
                    extendArr: [
                        {
                            type: "refresh",
                            text: "",
                            icon: "replay",
                            classPrefix: 'van-icon'
                        },
                        {
                            type: "clear",
                            text: "",
                            icon: "brush-o",
                            classPrefix: 'van-icon'
                        },
                    ]
                },
                messages: [],
                fileList: [],
                ip: "",
                page: 1,
            }
        },
        async mounted() {

            await this.getMyIp()
            this.getMessage()
        },
        methods: {
            /**
             * 
             * filevan-upload file
             */
            async upload(file) {
                try {
                    //  FormData 
                    const formData = new FormData();
                    formData.append('file', fileUtil.dataURLtoFileFun(file.content, file.file.name))
                    // config  headers 'Content-Type': 'multipart/form-data'
                    const config = {
                        headers: {'Content-Type': 'multipart/form-data'},
                    };
                    //  POST 
                    let loadToast = this.$toast.loading({
                        message: "...",
                        duration: 0
                    });
                    let rawRes = await axios.post(process.env.VUE_APP_BASE_URL + '/open/example/upload', formData, config);
                    loadToast.clear();
                    if (rawRes.data.code === 20000) {
                        this.$toast("");
                        return  rawRes.data.data;
                    } else {
                        this.$toast("")
                    }
                } catch (err) {
                    console.error(err.response.data);
                }
            },
            /**
             * ip
             *
             */
            async getMyIp() {
                let result = await api.getMyIp();
                this.ip = result.data;
            },
            /**
             * 
             */
            async getMessage(refresh, callback) {
                let result = await api.getMessageByPage(this.page, 10)
                if (result.length === 0) {
                    return;
                }
                //
                if (!this.messages) {
                    this.messages = []
                }
                if (refresh) {
                    //
                    this.messages = result.data
                } else {
                    //
                    this.messages = result.data.concat(this.messages)
                }
                this.messages = this.messages.map((item) => {
                    if (item.ip === this.ip) {
                        item.self = true
                    }
                    item.content.fileUrl =  process.env.VUE_APP_FILE_DOWN_URL + item.content.fileUrl
                    return item
                });
                if (callback) {
                    callback()
                }
            },
            async loadMore() {
                this.page++;
                //  POST 
                let loadToast = this.$toast.loading({
                    message: "...",
                    duration: 0
                });
                await this.getMessage()
                loadToast.clear();
            },
            async submit(msg) {
                let sendObj = {...msg};
                sendObj.time = time.getNowDateStr();
                sendObj.ip = this.ip;
                sendObj.content = {
                    text: "", // 
                    duration: "", // 
                    imageUrl: "", // 
                    videoUrl: "", // 
                    audioUrl: "", // 
                    fileUrl: "", // 
                    fileName: "", // 
                    fileSize: "", // 
                    fileExt: "", // 
                };
                sendObj.type = msg.type;
                // text|image|audio|video|file
                let tempUrl = ''
                switch (msg.type) {
                    case "text":
                        sendObj.content.text = msg.content;
                        break;
                    case "image":
                        sendObj.content.imageUrl = await this.upload(msg.content);
                        this.setFileInfo(sendObj, msg.content);
                        break;
                    case "audio":
                        sendObj.content.audioUrl = await this.upload(msg.content);
                        this.setFileInfo(sendObj, msg.content);
                        break;
                    case "video":
                        sendObj.content.videoUrl = await this.upload(msg.content);
                        this.setFileInfo(sendObj, msg.content);
                        break;
                    case "file":
                        tempUrl = await this.upload(msg.content);
                        console.log("url"+tempUrl);
                        sendObj.content.fileUrl = tempUrl;
                        this.setFileInfo(sendObj, msg.content);
                        break;

                    default:
                        console.error("" + msg.type);
                        return;
                }
                //
                let loadToast = this.loadToast();
                let result = await api.sendMessage(sendObj);
                loadToast.clear();
                if (result.code !== 20000) {
                    this.$toast.fail({
                        message: `${result.message}`,
                        closeOnClickOverlay: true
                    });
                } else {
                    this.$toast.success({
                        message: ``,
                        closeOnClick: true,
                        duration: 500
                    });
                    result.data.self = true;
                    this.messages.push(result.data)
                }

            },
            async clearMsg(){
                let result = await api.clearAll()
                console.log(result)
            },
            loadToast() {
                return this.$toast.loading();
            },
            /**
             * 
             */
            extendItemClick(item) {
                console.log('item', item)
                switch (item.type) {
                    case 'refresh':{
                        let loadToast = this.loadToast();
                        this.getMessage(true, () => {
                            loadToast.clear()
                        });

                        break;
                    }
                    case 'clear':
                        this.clearMsg().then(value => {
                            this.$toast.success({
                                message: ``,
                                closeOnClick: true,
                                duration: 500
                            });
                            this.getMessage(true);
                        })

                        break;
                    default:
                        console.error(""+item.type)
                }

                // 
                this.$refs.mChat.toggleExtend();
            },

            /**
             * 
             * @param sendObj
             * @param content
             */
            setFileInfo(sendObj, content) {
                sendObj.content.fileName = content.file.name;
                sendObj.content.fileSize = content.file.size/1024;
                sendObj.content.fileExt = content.file.name.split('.').pop();
            }
        }
    }
</script>

